<template>
  <div class="add-scene">
    <el-form
      :model="sceneForm"
      ref="sceneForm"
      label-width="120px"
      class="demo-ruleForm"
    >
      <el-form-item
        label="场景名称"
        prop="name"
        :rules="[{ required: true, message: '请输入场景名称', target: 'blur' }]"
      >
        <el-input
          v-model="sceneForm.name"
          maxlength="30"
          clearable
          placeholder="请输入场景名称"
        />
        <div class="tip">
          温馨提示：场景是对不同渠道下的细分，满足同一运营人员负责多个场景，<br />满足同一个场景不同运营人员负责
        </div>
      </el-form-item>
      <el-form-item
        label="场景值"
        prop="sceneValue"
        :rules="[{ required: true, message: '请输入场景值', target: 'blur' }]"
      >
        <el-input
          v-model="sceneForm.sceneValue"
          clearable
          placeholder="请输入场景名称"
        />
        <div class="tip">
          温馨提示：场景值是对生成码的标记，建议使用场景名称的首字母缩写<br />若场景值中有年月日，可显示：YYYY.MM.DD
        </div>
      </el-form-item>
      <el-form-item
        label="场景应用渠道"
        prop="channel"
        :rules="[{ required: true, message: '请选择', target: 'blur' }]"
      >
        <el-select
          v-model="sceneForm.channel"
          filterable
          clearable
          placeholder="请选择来源渠道"
        >
          <el-option
            v-for="item in channelList"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          />
        </el-select>
      </el-form-item>
      <el-form-item
        label="类型"
        prop="type"
        :rules="[{ required: true, message: '请选择', target: 'change' }]"
      >
        <el-radio-group
          v-model="sceneForm.type"
          :disabled="$route.name === 'editSource'"
        >
          <el-radio label="1">单人</el-radio>
          <el-radio label="2">多人</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item
        v-if="sceneForm.type === '2'"
        label="添加运营人员"
        prop="operator2"
        multiple
        filterable
        :rules="[
          { type: 'array', required: true, message: '请选择', target: 'change' }
        ]"
      >
        <el-select
          v-model="sceneForm.operator2"
          filterable
          multiple
          clearable
          placeholder="请选择运营人员"
        >
          <el-option
            v-for="(item, index) in operatorList"
            :key="index"
            :label="item.name"
            :value="item.userid"
          />
        </el-select>
      </el-form-item>
      <el-form-item
        v-else
        label="添加运营人员"
        prop="operator"
        filterable
        :rules="[{ required: true, message: '请选择', target: 'change' }]"
      >
        <el-select
          v-model="sceneForm.operator"
          filterable
          clearable
          placeholder="请选择运营人员"
        >
          <el-option
            v-for="(item, index) in operatorList"
            :key="index"
            :label="item.name"
            :value="item.userid"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="添加设置" prop="setUp">
        <el-checkbox-group v-model="sceneForm.setUp">
          <el-checkbox :label="true" name="yes"
            >客户添加时无需经过确认自动成为好友</el-checkbox
          >
        </el-checkbox-group>
      </el-form-item>
      <el-form-item
        v-if="this.$route.query.mode === 1 || this.$route.query.mode === '1'"
        label="按钮样式"
        prop="style"
        :rules="[{ required: true, message: '请选择', target: 'change' }]"
      >
        <el-radio-group v-model="sceneForm.style">
          <el-radio label="1">
            <img class="btn-style" :src="styleOne" alt="样式一" />
          </el-radio>
          <el-radio label="2">
            <img class="btn-style" :src="styleTwo" alt="样式二" />
          </el-radio>
          <el-radio label="3">
            <img class="btn-style" :src="styleThree" alt="样式三" />
          </el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item prop="setUp">
        <el-button type="primary" @click="submitForm('sceneForm')">
          {{ btnText }}
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {
  addSource,
  putSource,
  getUserType,
  getHelperList,
  getSourceDetail
} from '@/api/assistantManage'
import styleOne from '@/assets/images/style-one.png'
import styleTwo from '@/assets/images/style-two.png'
import styleThree from '@/assets/images/style-three.png'
export default {
  data() {
    return {
      btnText: this.$route.name === 'addSource' ? '确认新增' : '确认编辑',
      channelList: [],
      operatorList: [],
      styleOne: styleOne,
      styleTwo: styleTwo,
      styleThree: styleThree,
      sceneForm: {
        name: '',
        sceneValue: '',
        channel: '',
        type: '',
        operator: '',
        operator2: [],
        setUp: true,
        style: ''
      }
    }
  },
  computed: {
    getUser() {
      if (this.sceneForm.type === '1') {
        console.log(this.sceneForm.operator)
        return [
          {
            id: this.sceneForm.operator,
            name: this.operatorList.filter(v => {
              return this.sceneForm.operator === v.userid
            })[0].name
          }
        ]
      } else {
        return this.sceneForm.operator2.map(v => {
          const name = this.operatorList.filter(item => {
            return v === item.userid
          })[0].name
          console.log(name)
          return {
            id: v,
            name: name
          }
        })
      }
    },
    getUserId() {
      return function(user, num) {
        if (num === 1) {
          return user[0].id
        } else {
          return user.map(v => {
            return v.id
          })
        }
      }
    }
  },
  created() {
    if (this.$route.name === 'editSource') {
      this.getSourceDetail()
    }
    this.getUserType()
    this.getHelperList()
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.$route.params.id) {
            this.putSource()
          } else {
            this.addSource()
          }
        } else {
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    async addSource() {
      const params = {
        name: this.sceneForm.name,
        state: this.sceneForm.sceneValue,
        source_channel: this.sceneForm.channel,
        type: Number(this.sceneForm.type),
        skip_verify: this.sceneForm.setUp,
        user: this.getUser,
        style:
          Number(this.$route.query.mode) === 1
            ? Number(this.sceneForm.style)
            : undefined,
        mode: Number(this.$route.query.mode)
      }
      const { code, message } = await addSource(params)
      if (code === 10000) {
        this.$message.success(message)
        this.resetForm('sceneForm')
        this.$router.push({ name: 'channelScene' })
      }
    },
    async putSource() {
      const params = {
        name: this.sceneForm.name,
        state: this.sceneForm.sceneValue,
        source_channel: this.sceneForm.channel,
        type: Number(this.sceneForm.type),
        skip_verify: this.sceneForm.setUp,
        user: this.getUser,
        style:
          Number(this.$route.query.mode) === 1
            ? Number(this.sceneForm.style)
            : undefined,
        mode: Number(this.$route.query.mode)
      }
      const { code, message } = await putSource(this.$route.params.id, params)
      if (code === 10000) {
        this.$message.success(message)
        this.resetForm('sceneForm')
        this.$router.push({ name: 'channelScene' })
      }
    },
    async getUserType() {
      const { code, data } = await getUserType({ pageSize: 0 })
      if (code === 10000) {
        this.channelList = data.list.map(v => {
          return {
            name: `${v.first_name}-${v.second_name}${
              v.three_name ? '-' + v.three_name : ''
            }`,
            id: v._id
          }
        })
      }
    },
    async getHelperList() {
      const { code, data } = await getHelperList()
      if (code === 10000) {
        this.operatorList = data.filter(v => {
          return v.name
        })
      }
    },
    async getSourceDetail() {
      const { data, code } = await getSourceDetail(this.$route.params.id)
      if (code === 10000) {
        this.operatorList = data.user.map(v => {
          return {
            userid: v.id,
            name: v.name
          }
        })
        this.sceneForm = {
          name: data.name,
          sceneValue: data.state,
          channel: data.source_channel,
          type: data.type + '',
          operator: data.type === 1 ? this.getUserId(data.user, 1) : '',
          operator2: data.type === 2 ? this.getUserId(data.user, 2) : [],
          setUp: data.skip_verify,
          style: data.style + ''
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.add-scene {
  /deep/.el-input {
    width: 220px;
    input {
      height: 32px;
      line-height: 32px;
    }
  }
  .tip {
    margin-top: 10px;
    line-height: 16px;
  }
  .btn-style {
    border: 1px solid rgba(228, 230, 233, 1);
    border-radius: 4px;
    vertical-align: middle;
  }
}
</style>
